export default {
	name:"KButton",
	props:{
		 /* 内置按钮风格 */
		type: {//按钮状态
			type: String,
			default: 'normal'
		},
		// text: {//文字
		// 	type: String,
			
		// },
		plain:{//朴素按钮
			type: Boolean,
		},
		round:{//圆角
			type: Boolean,
		},
		disabled:{//可选
			type: Boolean,
		},
		size:{//尺寸
			type: String,
			default: 'small'
		}
	},
	computed: {
		/* 根据不同的props，展示按钮样式 */
		styles: {
			get() {
				return ['sp-button',  this.type, this.plain?'plain':'',this.size,this.round?'round':'',this.disabled?'disabled':'']
			}
		}
	},
	data() {
	return {

	};
	}
}
